import React from 'react'
import './index.less'
import NoneData from 'components/NoneData/all-none'
import connect from 'connect'
import { Checkbox, Flex, NavBar, WhiteSpace, WingBlank, Button, List, Grid, Card, Stepper } from 'antd-mobile';
import {withRouter, Redirect} from 'react-router-dom'
@withRouter
@connect
class Home extends React.Component {
    constructor(props){
        super(props);
        
        this.state = {
          navTop: false,
          swiper: null,
          value: '美食',
          data: ['1', '2', '3'],
          imgHeight: 176,
          disabled: false,
          val: 2
        }

        this.$tab = null;
        this.offsetTop = 0;
    }

    
    componentDidMount () {
       setTimeout(() => {
             this.setState({
               data: ['AiyWuByWklrrUDlFignR', 'TekJlZRVCjLFexlOCuWn', 'IJOtIlfsYdTyaDTRVrLI'],
             });
           }, 100);
    }


      onChange= (value) => {
        this.setState({ value });
      };
      clear = () => {
        this.setState({ value: '' });
      };
      handleClick = () => {
        this.manualFocusInst.focus();
    }

    handleScroll(){
        let sTop = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop;

        if(!this.state.navTop && sTop >= this.offsetTop){
           this.setState({
             navTop: true
           })
        }

        if(sTop < this.offsetTop){
           this.setState({
             navTop:false
           })
        }
      }

    togriditem(index, item) {
      switch(index){
          case 0: { /// 本周最热
              this.props.history.slideStatus = 'left'
              this.props.history.push(`/collect`)
          }
          break;
          case 1:  { /// 我的收藏
              this.props.history.slideStatus = 'left'
              this.props.history.push(`/address`)
          }
          break;
          case 2: { /// 线下活动
              this.props.history.slideStatus = 'left'
              this.props.history.push(`/system`)
          }
          break;
          case 3: {/// 专栏
              this.props.history.slideStatus = 'left'
              this.props.history.push(`/aboutme`)
          }
          break;
          
      }
    }

    toordertype(index, item) {
      switch(index){
          case 0: { /// 本周最热
              this.props.history.slideStatus = 'left'
              this.props.history.push(`/myorder/1`)
          }
          break;
          case 1:  { /// 我的收藏
              this.props.history.slideStatus = 'left'
              this.props.history.push(`/myorder/2`)
          }
          break;
          case 2: { /// 线下活动
              this.props.history.slideStatus = 'left'
              this.props.history.push(`/myorder/3`)
          }
          break;
          case 3: {/// 专栏
              this.props.history.slideStatus = 'left'
              this.props.history.push(`/myorder/4`)
          }
          break;
          case 4: {/// 专栏
              this.props.history.slideStatus = 'left'
              this.props.history.push(`/myorder/5`)
          }
          break;
          
      }
    }

    renderTabBar(props) {
      return (<div>
        {({ style }) => <div style={{ ...style, zIndex: 1 }}><Tabs.DefaultTabBar {...props} /></div>}
      </div>);
    }

    onChange2 = (val) => {
        // console.log(val);
        this.setState({ val });
      }
      onChange1 = (val1) => {
        // console.log(val);
        this.setState({ val1 });
      }


    render () {
      const data = [
      	{
      		icon: require('../../images/wd_qb.png'),
      		text: '全部',
      	},
      	{
      		icon: require('../../images/wd_dfk.png'),
      		text: '待付款',
      	},
      	{
      		icon: require('../../images/wd_dfh.png'),
      		text: '待发货',
      	},
      	{
      		icon: require('../../images/wd_dsh.png'),
      		text: '待收货',
      	},
      	{
      		icon: require('../../images/wd_dpj.png'),
      		text: '待评价',
      	},
      ]

      const data1 = [
      	{
      		icon: require('../../images/wd_sc.png'),
      		text: '收藏',
      	},
      	{
      		icon: require('../../images/wd_dz.png'),
      		text: '地址',
      	},
      	{
      		icon: require('../../images/wd_sz.png'),
      		text: '设置',
      	},
      	{
      		icon: require('../../images/wd_sz1.png'),
      		text: '关于',
      	},
      ]

      const data22 = [
            { value: 0, label: 'Ph.D.' },
            { value: 1, label: 'Bachelor' },
            { value: 2, label: 'College diploma' },
          ];
        const Item = List.Item;
        const Brief = Item.Brief;
        const CheckboxItem = Checkbox.CheckboxItem;
        const AgreeItem = Checkbox.AgreeItem;
        const {newsList, newsIndex, recommend, daily, banner, classify} = this.props.state.home
        const params = {
              spaceBetween: 30,
              centeredSlides: true,
              autoplay: {
                delay: 1000,
                disableOnInteraction: true
              },
              pagination: {
                el: '.swiper-pagination',
                clickable: true,
              }
            };
        this.state.swiper && this.state.swiper.slideTo(newsIndex)
        return (
            <div className="account-container">
              <div className="center">
              <img src={require('../../images/wd_bg.png')} className="bg" />
              <img src={require('../../images/wd_metx.png')} className="avator bgcenter" />
              <div className="name">昵称昵称昵称昵称 </div>
              </div>
              <div className="black">
              </div>
              <Grid data={data}
                    columnNum={5}
                    hasLine={false}
                    renderItem={(dataItem, index) => (
                      <div style={{ padding: '12.5px 0', height: '0.5rem'}} onClick={() => {this.toordertype(index, dataItem)}}>
                        <img src={dataItem.icon} style={{ width: '0.25rem', height: 'auto', minHeight: '0.25rem' }} alt="icon" />
                        <div style={{ color: '#333', fontSize: '0.12rem', lineHeight: '0.2rem',}}>
                          <span>{dataItem.text}</span>
                        </div>
                      </div>
                    )}
                  />
               <div className="black">
               </div>
                <div className="blackborder">
                </div>
               <div className="black">
               </div>
               <Grid data={data1}
                     columnNum={4}
                     hasLine={false}
                     renderItem={(dataItem, index) => (
                       <div style={{ padding: '12.5px 0', height: '0.5rem'}} onClick={() => {this.togriditem(index, dataItem)}}>
                         <img src={dataItem.icon} style={{ height: '0.25rem', width: 'auto', minWidth: '0.2rem' }} alt="icon" />
                         <div style={{ color: '#333', fontSize: '0.14rem', lineHeight: '0.2rem',}}>
                           <span>{dataItem.text}</span>
                         </div>
                       </div>
                     )}
                   />
            </div>
        )
    }
}
export default Home


