import { TabBar } from 'antd-mobile';
import React from 'react'
import './index.less'
import Home from '../Home/index'
import Classify from '../classify/index'
import Account from '../Account/index'
import Gouwuche from '../gouwuche/index'

class Container extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedTab: 'home',
      hidden: false,
      fullScreen: true,
    };
  }


  render() {
    return (
      <div style={{ position: 'fixed', height: '100%', width: '100%', top: 0 }}>
        <TabBar
          unselectedTintColor="#999"
          tintColor="#B4917B"
          barTintColor="white"
          hidden={this.state.hidden}
          tabBarPosition='bottom'
        >
          <TabBar.Item
            title="首页"
            key="首页"
            icon={<div style={{
              width: '22px',
              height: '22px',
              background: 'url('+require('../../images/sy1.png')+') center center /  21px 21px no-repeat' }}
            />
            }
            selectedIcon={<div style={{
              width: '22px',
              height: '22px',
              background: 'url('+require('../../images/sy2.png')+') center center /  21px 21px no-repeat' }}
            />
            }
            selected={this.state.selectedTab === 'home'}
            onPress={() => {
              this.setState({
                selectedTab: 'home',
              });
            }}
            data-seed="logId"
          >
            <Home/>
          </TabBar.Item>
          <TabBar.Item
            icon={
              <div style={{
                width: '22px',
                height: '22px',
                background: 'url('+require('../../images/fenlei1.png')+') center center /  21px 21px no-repeat' }}
              />
            }
            selectedIcon={
              <div style={{
                width: '22px',
                height: '22px',
                background: 'url('+require('../../images/fenlei2.png')+') center center /  21px 21px no-repeat' }}
              />
            }
            title="分类"
            key="分类"
            selected={this.state.selectedTab === 'classify'}
            onPress={() => {
              this.setState({
                selectedTab: 'classify',
              });
            }}
            data-seed="logId1"
          >
            <Classify/>
          </TabBar.Item>
          <TabBar.Item
            icon={
              <div style={{
                width: '22px',
                height: '22px',
                background: 'url('+require('../../images/gouwuceh1.png')+') center center /  21px 21px no-repeat' }}
              />
            }
            selectedIcon={
              <div style={{
                width: '22px',
                height: '22px',
                background: 'url('+require('../../images/gouwuceh2.png')+') center center /  21px 21px no-repeat' }}
              />
            }
            title="购物车"
            key="购物车"
            selected={this.state.selectedTab === 'gouwuche'}
            onPress={() => {
              this.setState({
                selectedTab: 'gouwuche',
              });
            }}
          >
            <Gouwuche/>
          </TabBar.Item>
          <TabBar.Item
            icon={
              <div style={{
                width: '22px',
                height: '22px',
                background: 'url('+require('../../images/wode1.png')+') center center /  21px 21px no-repeat' }}
              />
            }
            selectedIcon={
              <div style={{
                width: '22px',
                height: '22px',
                background: 'url('+require('../../images/wode2.png')+') center center /  21px 21px no-repeat' }}
              />
            }
            title="我的"
            key="my"
            selected={this.state.selectedTab === 'my'}
            onPress={() => {
              this.setState({
                selectedTab: 'my',
              });
            }}
          >
            <Account/>
          </TabBar.Item>
        </TabBar>
      </div>
    );
  }
}

export default Container



